<template>
	<view class="uni-list">
		<view class="uni-list-cell">
			<view class="uni-list-cell-db">
				<picker @change="bindPickerChange" :value="index" :range="list">
					<view class="uni-input">{{list[index]}}</view>
				</picker>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			list: {
				type: Array
			}
		},
		data() {
			return {
				index: 0,
			}
		},
		methods: {
			bindPickerChange: function(e) {
				console.log('picker发送选择改变，携带值为', e.detail.value)
				this.index = e.detail.value
			},
		}
	}
</script>

<style>
	.uni-list-cell {
		margin-top: 10px;
		border: 1px solid rgb(223, 223, 224);
		height: 40px;
		line-height: 40px;
		font-size: 20px;
		text-align: center;
		border-radius: 5px;

	}

	.uni-list-cell-left {
		margin-right: 5px;
	}
</style>